import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import './list.css'
import NameContainer from './NameContainer';

export default class List extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isFatched: false,
            items: [],
            searchTerm: '',
        }
    }

    componentDidMount() {
        const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const url = "https://s3-ap-southeast-1.amazonaws.com/he-public-data/users49b8675.json"
        fetch(proxyurl + url)
            .then(res => res.json())
            .then(r => {
                this.setState({
                    isFatched: true,
                    items: r,
            })
        })
        
        
    }

    searchTermChange = (e) => {
        this.setState({
            searchTerm: e.target.value
        })
    }

    dynamicSearch = () => {
        return this.state.items.filter(item => {
            return item.name.toLowerCase().includes(this.state.searchTerm.toLocaleLowerCase())
        })
            
            // name.toLowerCase().include(this.state.searchTerm.toLowerCase()))
    }
    render() {
        return (
            <>
                <div style={{ textAlign: 'center' }}>
    
                    <div className="nav"> 

                    <input type="text" value={this.state.searchTerm} onChange={this.searchTermChange} placeholder="Search For a name"></input>
                    {/* <Link to="/shortlisted" className="nav-button">ShortListed</Link>
                        <Link to="/rejected" className="nav-button">Rejected</Link> */}
                    </div>
                    <NameContainer data = {this.dynamicSearch()}></NameContainer>
                </div>
        
            </>
        )
    }

}
