import React, { Component } from 'react'

export default class NameContainer extends Component {
    render() {
        return (
            <div>

                {this.props.data.map(
                    item => {
                        return (
                            <div className="card" key={item.id}>
                                <img src={item.Image} alt="" />
                                <h1>{ item.name}</h1>
                            </div>
                        )
                    }
                )}
            </div>
        )
    }
}

